<template>
  <div>
    <div class="d-flex justify-center pa-3 tex-h3-h4 font-weight-bold">
      {{ artInfo.title }}
    </div>
    <div class="d-flex justify-center">
      <v-icon>{{ "mdi-calender-month" }}</v-icon>
      <span>{{ artInfo.CreatedAt | dateformat("YYYY-MM-DD HH:SS") }}</span>
    </div>
    <v-divider class="pa-3 ma-3"></v-divider>
    <v-alert
      color="indigo"
      dark
      border="left"
      outlined
      class="ma-4"
      elevation="1"
      >{{ artInfo.desc }}</v-alert
    >
    <div class="content ma-5 pa-3 text-justify">
      <Viewer :value="artInfo.content" :plugins="plugins"></Viewer>
    </div>
  </div>
</template>

<script>
import gfm from "@bytemd/plugin-gfm";
import { Viewer } from "@bytemd/vue";
import highlight from "@bytemd/plugin-highlight";

import "bytemd/dist/index.css";
import "highlight.js/styles/default.css";

const plugins = [
  gfm(),
  // Add more plugins here
  highlight(),
];

export default {
  components: { Viewer },
  props: ["id"],
  data() {
    return {
      plugins,
      artInfo: {},
    };
  },
  created() {
    this.getArtInfo(this.id);
  },
  methods: {
    //
    async getArtInfo(id) {
      const { data: res } = await this.$http.get(`/article/info/${id}`);
      if (res.status != 200) return this.$message("error", res.message);
      this.artInfo = res.data;
      this.artInfo.id = res.data.ID;
    },
  },
};
</script>

<style scoped>
.content img,
span {
  width: 900%;
}
</style>